.container_marquee {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  white-space: nowrap;
  background-color: var(--warna-biru);
  color: var(--warna-cream);
}
.container_marquee h2 {
  font-size: 18vw;
}
@media screen and (min-width: 64rem) {
  .container_marquee h2 {
    font-size: 10vw;
  }
}